// Dependencies
import React from 'react'
import { List, Typography, CircularProgress, TextField, Button } from '@material-ui/core';
import { observer } from 'mobx-react';

// Components
import User from './User';

// Dto
import { IUser } from '../../store/users/user.store';
import FlatList from '../FlatList';

/**
 * 
 * @param props 
 */
export function Users(props: any): React.ReactElement {
  const { addUser, loading, selectedUser, data } = props;
  const [name, setName] = React.useState('');
  const onChange = React.useCallback((event) => {
    const { target } = event;
    setName(target.value);
  }, [])
  const onAddClick = React.useCallback(async () => {
    await addUser({ name });
    setName('')
  }, [addUser, name]);

  return (
    <React.Fragment>
      <Typography variant="h4">Users</Typography>
      {loading && <CircularProgress />}
      <List>
        {
          data.map((user: IUser) => (
            <User
              key={user.id}
              isSelected={selectedUser?.id === user.id}
              name={user.name}
              selectUser={user.select}
              deleteUser={user.remove}
              updateUser={user.update}
            />
          ))
        }
      </List>
      <TextField
        label="Name"
        value={name}
        onChange={onChange}
      />
      <Button
        onClick={onAddClick}
        variant="contained"
        color="primary"
        disabled={!name}
      >
        Add User
      </Button>
    </React.Fragment>
  )
}

export default observer(Users);