import React from 'react';
import { Checkbox } from '@material-ui/core';
import Item from '../../Item';
import { ITask } from '../../../store/tasks/task.store';

export interface TodoProps {
  text: string;
  isDone: boolean;
  toggle: () => void;
  deleteTask: () => void;
  updateTask: (user: Partial<ITask>) => Promise<ITask>
}

export function Todo(props: TodoProps): React.ReactElement {
  const { text, isDone, toggle, deleteTask, updateTask } = props;
  const onDeleteTask = React.useCallback(() => deleteTask(), [deleteTask]);
  const onUpdateTask = React.useCallback((description) => updateTask({ description }), [updateTask])

  return (
    <Item
      text={text}
      onUpdate={onUpdateTask}
      onDelete={onDeleteTask}
      options={[
        <Checkbox
          key='toggle'
          checked={isDone}
          onChange={toggle}
          inputProps={{ 'aria-label': 'primary checkbox' }}
        />
      ]}
    />
  );
}

export default React.memo(Todo);