// Dependencies
import React from 'react';
import { ListItem, ListItemSecondaryAction, IconButton, ListItemText } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/DeleteForever';
import EditIcon from '@material-ui/icons/Edit';
import SaveIcon from '@material-ui/icons/Save';
import CancelIcon from '@material-ui/icons/Cancel';

import TextField from '@material-ui/core/TextField';
import { useStateActions } from '../../hooks';
import Confirm from '../Confirm';

export interface ItemProps {
  text: string;
  isSelected: boolean;
  onClick?: () => void;
  onDelete: () => void;
  onUpdate: (inputValue: string) => void;
  options?: React.ReactNode
}

export function Item(props: ItemProps): React.ReactElement {
  const { text, onClick, onUpdate, onDelete, isSelected, options } = props;
  const [showDelete, openDelete, closeDelete] = useStateActions();
  const [isEdit, changeToEdit, changeToRead] = useStateActions();
  const [inputValue, setName] = React.useState(text);

  const onChange = React.useCallback((event) => {
    const { target } = event;
    setName(target.value);
  }, []);

  const onDeleteUser = React.useCallback(() => {
    closeDelete();
    onDelete();
  }, [onDelete, closeDelete]);

  const onUpdateUser = React.useCallback(() => {
    onUpdate(inputValue);
    changeToRead();
  }, [onUpdate, inputValue, changeToRead])

  return (
    <React.Fragment>
      {
        isEdit
          ? (
            <ListItem dense button selected={isSelected}>
              <TextField
                autoFocus
                margin="dense"
                label="Name"
                fullWidth
                value={inputValue}
                onChange={onChange}
              />
              <ListItemSecondaryAction>
                <IconButton aria-label="save" onClick={onUpdateUser}>
                  <SaveIcon />
                </IconButton>
                <IconButton aria-label="read" onClick={changeToRead}>
                  <CancelIcon />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          ) : (
            <ListItem dense button onClick={onClick} selected={isSelected}>
              <ListItemText primary={text} />
              <ListItemSecondaryAction>
                {options}
                <IconButton aria-label="edit" onClick={changeToEdit}>
                  <EditIcon />
                </IconButton>
                <IconButton aria-label="delete" onClick={openDelete}>
                  <DeleteIcon />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          )
      }
      <Confirm open={showDelete} onClose={closeDelete} onConfirm={onDeleteUser} />
    </React.Fragment>
  );
}

Item.defaultProps = {
  isSelected: false,
};

export default React.memo(Item);