// Dependencies
import React from 'react'
import { List, Typography, CircularProgress, TextField, Button } from '@material-ui/core';
import { observer } from 'mobx-react';

// Dto
import FlatList from '../FlatList';
import { ITask } from '../../store/tasks/task.store';
import Task from './Task';
import { toJS } from 'mobx';

/**
 * 
 * @param props 
 */
export function Tasks(props: any): React.ReactElement {
  const { addTask, loading, data, taskOwner } = props;
  const [description, setDescription] = React.useState('');
  const onChange = React.useCallback((event) => {
    const { target } = event;
    setDescription(target.value);
  }, [])
  const onAddClick = React.useCallback(async () => {
    await addTask(taskOwner.id, { description });
    setDescription('')
  }, [addTask, description, taskOwner]);

  return (
    <React.Fragment>
      <Typography variant="h4">Tasks</Typography>
      {loading && <CircularProgress />}
      <List>
        {
          data.map((task: ITask) => (
            <Task
              key={task.id}
              text={task.description}
              isDone={task.isDone}
              toggle={task.toggle}
              deleteTask={task.remove}
              updateTask={task.update}
            />
          ))
        }
      </List>
      <TextField
        label="Name"
        value={description}
        onChange={onChange}
        fullWidth
      />
      <Button
        onClick={onAddClick}
        variant="contained"
        color="primary"
        disabled={!description}
      >
        Add task
      </Button>
    </React.Fragment>
  )
}

export default observer(Tasks);