// Dependencies
import React from 'react';

// Hooks
import { IUser } from '../../../store/users/user.store';
import Item from '../../Item';

export interface UserProps {
  name: string;
  isSelected: boolean;
  selectUser: () => void;
  deleteUser: () => void;
  updateUser: (user: Partial<IUser>) => Promise<IUser>
}

export function User(props: UserProps): React.ReactElement {
  const { name, selectUser, updateUser, deleteUser, isSelected } = props;
  const onDeleteUser = React.useCallback(() => deleteUser(), [deleteUser]);
  const onUpdateUser = React.useCallback((value) => updateUser({ name: value }), [updateUser])

  return (
    <Item
      text={name}
      onClick={selectUser}
      isSelected={isSelected}
      onUpdate={onUpdateUser}
      onDelete={onDeleteUser}
    />
  );
}

export default React.memo(User);